import * as uuid from 'uuid';

function endpoint(api) {
    return 'https://dogedock.2gether.video:5001' + api
}

export const Actions = {
    Types: {
        REFRESH_CHAT_LIST: "REFRESH_CHAT_LIST",
        JOIN_CHAT: "JOIN_CHAT",
        UPDATE_CURRENT_CHAT_DETAIL: "UPDATE_CURRENT_CHAT_DETAIL",
        UPDATE_LIBRARY_PLAYLISTS: "UPDATE_LIBRARY_PLAYLISTS",
        OPEN_PLAYLIST: "OPEN_PLAYLIST",
        UPDATE_CURRENT_OPNED_PLAYLIST: "UPDATE_CURRENT_OPNED_PLAYLIST",
        UPDATE_MUSIC_PLAYER_DETAIL: "UPDATE_MUSIC_PLAYER_DETAIL",
        APPEND_CURRENT_CHAT_MESSAGE: "APPEND_CURRENT_CHAT_MESSAGE",
        UPDATE_USER_ID: "UPDATE_USER_ID"
    },

    DispathUpdateChatListAction(chatList, dispatch) {
        dispatch({ type: this.Types.UPDATE_CHAT_LIST, data: chatList });
    },
    ExitChat(dispatch) {
        dispatch({ type: "EXIT_CHAT" })
    },
    JoinChat(id, dispatch) {
        this.ExitChat(dispatch);
        dispatch({
            type: this.Types.JOIN_CHAT, data: {
                Id: id,
                ScheduledTaskId: setInterval(() => {
                    this.FetchChatAsync(id, dispatch);
                }, 10000)
            }
        })
        this.FetchChatAsync(id, dispatch);
    },
    UpdateMusicPlayerDetail(progress, name, image, dispatch) {
        dispatch({
            type: this.Types.UPDATE_MUSIC_PLAYER_DETAIL, data: {
                Progress: progress,
                Name: name,
                Image: image
            }
        });
    },
    async OpenPlaylistAsync(id, dispatch) {
        dispatch({ type: this.Types.OPEN_PLAYLIST, data: id });
        try {
            let playlist = await MusicKit.getInstance().api.library.playlist(id);
            dispatch({ type: this.Types.UPDATE_CURRENT_OPNED_PLAYLIST, data: playlist })
        } catch (e) {
            console.error(e);
        }
    },
    async CreateChatAsync(songs, name, state, dispatch) {
        let chat = await (await fetch(endpoint('/chat/update'), {
            method: "POST",
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                Songs: songs,
                Name: name
            })
        })).json();
        console.log(chat);
        this.JoinChat(chat.ID, dispatch);
    },
    async FetchChatAsync(id, dispatch) {
        let chat = await (await fetch(endpoint('/chat/get?id=' + id))).json();
        dispatch({ type: this.Types.UPDATE_CURRENT_CHAT_DETAIL, data: chat });
    },
    async FetchMessageAsync(ChatID, Offset, dispatch) {
        let message = await (await fetch(endpoint(`/message/get?ChatID=${ChatID}&Offset=${Offset}`))).json();
        dispatch({ type: this.Types.APPEND_CURRENT_CHAT_MESSAGE, data: message })
    },
    FetchChatList(offset, limit, state, dispatch) {
        this.DispathUpdateChatListAction([...state.ChatList, ...Array(limit).map(i => { })], dispatch);
    },

    async RefreshChatListAsync(limit, dispatch) {
        let chats = await (await fetch(endpoint('/chats'))).json();
        dispatch({ type: this.Types.REFRESH_CHAT_LIST, data: chats });
    },
    async FetchLibraryPlayListsAsync(state, dispatch) {
        let playlists = (await MusicKit.getInstance().api.library.playlists());
        console.log(playlists)
        dispatch({ type: this.Types.UPDATE_LIBRARY_PLAYLISTS, data: playlists });
    },
    async SendMessageAsync(text, state, dispatch) {
        if (localStorage.getItem("UserId") == null) {
            localStorage.setItem("UserId", uuid.v4());
        }

        let userId = localStorage.getItem("UserId");

        if (state.User?.Id == null) {
            dispatch({ type: this.Types.UPDATE_USER_ID, data: localStorage.getItem("UserId") })
        }

        await fetch(endpoint('/message/send'), {
            method: "POST",
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                Text: text,
                SenderId: userId,
                SenderName: userId,
                ChatID: state.CurrentChat.Id
            }),
        })
    }
}