import React, { useContext, useEffect, useRef, useState } from 'react';

import {
  Block, BlockTitle, f7, List, ListItem, Page, Navbar, Button,
} from 'framework7-react';

import AppstorePage from '../components/AppstorePage';
import { StateContext } from '../state/StateContext';
import { Actions } from '../state/Actions';

const Search = () => {
  const { state, dispatch } = useContext(StateContext);

  useEffect(() => {
    Actions.RefreshChatListAsync(10, dispatch);
  }, [])

  const allowInfinite = useRef(true);

  const [showPreloader, setShowPreloader] = useState(true);

  const loadMore = () => {
    console.log("load")
    if (!allowInfinite.current) return;
    allowInfinite.current = false;

    //if (state.ChatList.length >= 50) {
      setShowPreloader(false);
      return;
    //}
    // dispatch({ type: "FETCH_MORE_CHAT_LIST" });
    allowInfinite.current = true;

  };

  return (
    <AppstorePage title="">
      <Page ptr ptrMousewheel={true} onPtrRefresh={(done) => { console.log(123); Actions.RefreshChatListAsync(10, dispatch); done() }}
        infinite infiniteDistance={50} infinitePreloader={showPreloader} onInfinite={loadMore}>
        <Block> </Block>
        <Button onClick={() => {
          MusicKit.getInstance().authorize();
        }}> Apple Music</Button>
        <List mediaList>
          {
            state.ChatList.map(chat => (
              <ListItem
                onClick={() => {
                  if(state.CurrentChat?.Id != chat.ID){
                    Actions.JoinChat(chat.ID, dispatch);
                    dispatch({ type: "SHOW_CHAT_MESSAGE_WINDOW" });
                  }else{
                    dispatch({ type: "SHOW_CHAT_MESSAGE_WINDOW" });
                  }
                }}
                link='#'
                title={chat.Name}
                subtitle="music name">
                <img
                  slot="media"
                  src="https://2gether.video/logo.png"
                  width="80"
                />
              </ListItem>
            ))
          }
        </List>
      </Page>

    </AppstorePage>
  );
};

export default Search;
