import React, { useContext, useEffect, useRef, useState } from 'react';
import './Chat.less';
import ReactJkMusicPlayer from 'react-jinke-music-player';
import 'react-jinke-music-player/assets/index.css';

import {
  Block,
  Button,
  Input,
  Range,
  Progressbar,
  Row,
  Col,
  Navbar,
  Page,
  Messages,
  Icon,
  ListItem,
  MessagesTitle,
  ListItemCell,
  Message,
  Messagebar,
  Link,
  List,
  View,
  MessagebarAttachments,
  MessagebarAttachment,
  MessagebarSheet,
  MessagebarSheetImage,
  f7ready,
  f7,
} from 'framework7-react';

import AppstorePage from '../components/AppstorePage';
import { Actions } from '../state/Actions';
import { StateContext } from '../state/StateContext';

const Chat = () => {
  const { state, dispatch } = useContext(StateContext);

  const [messageText, setMessageText] = useState('');
  const sendMessage = async () => {
    if (messageText.length > 0) {
      await Actions.SendMessageAsync(messageText, state, dispatch);
      setMessageText("");
    }
  };


  return (
    <Page noToolbar className='chat-page'>
      {/* <Navbar className='chat-music-navbar'>
        <div className='title' style={{ width: "100%" }}>
          <Row noGap className='chat-music-player' style={{ display: "flex" }}>
            <img
              slot="media"
              src="https://cdn.framework7.io/placeholder/people-160x160-1.jpg"
              width="96px"
              height='96px'
            />
            <Row noGap>
              <Col width='100' style={{ height: '96px' }}>
                <Row noGap style={{ height: '92px' }}>
                  <Col width='50'>
                    <p>Room Title</p>
                    <p>Music Title</p>
                  </Col>
                  <Col width='50' style={{ height: '92px' }}>
                    <Row noGap style={{ height: '56px' }} >
                    </Row>
                    <Row noGap style={{ height: '36px' }} >

                      <Col width='25'>
                        <Button className=' back'>Exit</Button>
                      </Col>

                      <Col width='25'>
                        <Button iconF7="person_2_fill" >
                        </Button>
                      </Col>
                      <Col width='25'>
                        <Button iconF7="list_number" >
                        </Button>
                      </Col>

                      <Col width='25'>
                        <Button iconF7="chat_bubble_fill" fill />

                      </Col>

                    </Row>
                  </Col>

                </Row>
                <Row style={{ height: '4px' }}>
                  <Progressbar style={{ height: '4px' }} progress={10} id="demo-inline-progressbar" />
                </Row>
              </Col>
            </Row>
          </Row>
        </div>
      </Navbar> */}
      <Messagebar
        value={messageText}
        onInput={(e) => setMessageText(e.target.value)}
      >
        <Link
          iconIos="f7:arrow_up_circle_fill"
          iconAurora="f7:arrow_up_circle_fill"
          iconMd="material:send"
          slot="inner-end"
          onClick={sendMessage}
        />
      </Messagebar>

      <Messages>
        <MessagesTitle>
          {/* <b>Sunday, Feb 9,</b> 12:58 */}
        </MessagesTitle>

        {state.CurrentChat?.Messages?.map((message, index) => (
          <Message
            key={index}
            type={message.SenderId == state.User?.Id ? 'sent' : 'received'}
            name={message.SenderId}
            // avatar='https://cdn.framework7.io/placeholder/people-100x100-9.jpg'
            first={true}
            last={true}
            tail={true}
          >
            {message.Text && (
              <span slot="text">{message.Text}</span>
            )}
          </Message>
        ))}
        {/* {typingMessage && (
          <Message
            type="received"
            typing={true}
            first={true}
            last={true}
            tail={true}
            header={`${typingMessage.name} is typing`}
            avatar={typingMessage.avatar}
          />
        )} */}
      </Messages>
    </Page>
  );
};

export default Chat;
