import React, { useContext, useEffect, useRef, useState } from 'react';

import {
    Block,
    Button,
    Input,
    Navbar,
    Page,
    Messages,
    MessagesTitle,
    Message,
    Messagebar,
    Link,
    View,
    MessagebarAttachments,
    MessagebarAttachment,
    MessagebarSheet,
    MessagebarSheetImage,
    f7ready,
    f7,
    List,
    ListItem,
    Row,
    Col,
    Popup,
    NavRight
} from 'framework7-react';

import AppstorePage from '../components/AppstorePage';
import { Actions } from '../state/Actions';
import { StateContext } from '../state/StateContext';

const LibraryPlaylist = () => {
    const { state, dispatch } = useContext(StateContext);
    return (
        <Popup className="popup-library-playlist" swipeToClose>
            <Page>
                <Navbar title="Songs">
                    <NavRight>
                        <Link popupClose>Close</Link>
                    </NavRight>
                </Navbar>

                <Button onClick={async () => {
                    console.log(state.OpenedPlaylist?.Playlist?.relationships?.tracks?.data);

                    if (state.OpenedPlaylist.Playlist != null) {
                        let songs = state.OpenedPlaylist?.Playlist?.relationships?.tracks?.data?.map(song =>
                        ({
                            SID: song.attributes?.playParams?.catalogId,
                            Duration: song.attributes.durationInMillis / 1000,
                            Name: song.attributes?.name
                        })).filter(song => song.SID != null);
                        console.log(songs);
                        await Actions.CreateChatAsync(songs, state.OpenedPlaylist?.Playlist.attributes.name, state, dispatch);
                        f7.popup.close(".popup-library-playlist");
                    }
                }}></Button>

                <List mediaList>
                    {
                        state.OpenedPlaylist?.Playlist?.relationships?.tracks?.data?.map(song => (
                            <ListItem key={song.id} title={song?.attributes?.name}>
                                <img style={{ width: "80px", height: "80px" }} slot="media" src={song.attributes?.artwork?.url.replace('{w}x{h}', '80x80')} alt="" />
                            </ListItem>
                        )
                        )
                    }
                </List>
            </Page>
        </Popup>
    )
}
export default LibraryPlaylist;