/* eslint-disable react/jsx-props-no-spreading */
import React, { useState, useEffect, useRef, useReducer } from 'react';
import $ from 'dom7';
import { StateContext } from '../state/StateContext';
import {
  f7,
  App,
  Views,
  View,
  Toolbar,
  Link,
  ListItem,
  Navbar, Col, Row, Button, Progressbar, Popup, Page, NavRight, List, Block
} from 'framework7-react';

import PWA from '../js/pwa';
import routes from '../js/routes';
import { Actions } from '../state/Actions';

function ActionReducer(state, action) {
  let type = action.type;
  switch (type) {
    case Actions.Types.REFRESH_CHAT_LIST: {
      return {
        ...state,
        ChatList: action.data
      }
    }
    case 'FETCH_MORE_CHAT_LIST': {
      return {
        ...state,
        ChatList: [...state.ChatList, {}, {}, {}, {}, {}, {}, {}, {}, {}, {}],
      }
    }
    case Actions.Types.JOIN_CHAT: {
      if (state.CurrentChat?.ScheduledTaskId != null) {
        clearInterval(state.CurrentChat?.ScheduledTaskId);
      }
      return {
        ...state,
        CurrentChat: {
          Id: action.data.Id,
          ScheduledTaskId: action.data.ScheduledTaskId
        }
      }
    }
    case Actions.Types.OPEN_PLAYLIST: {

      return {
        ...state,
        OpenedPlaylist: {
          Id: action.data,
          Loadding: true,
          Playlist: null
        }
      }
    }
    case Actions.Types.UPDATE_CURRENT_OPNED_PLAYLIST: {
      return {
        ...state,
        OpenedPlaylist: {
          Id: action.data,
          Loadding: false,
          Playlist: action.data
        }
      }
    }
    case Actions.Types.UPDATE_CURRENT_CHAT_DETAIL: {
      return {
        ...state,
        CurrentChat: {
          ...state.CurrentChat,
          Id: action.data.ID,
          LastUpdateTimestamp: action.data.LastUpdateTimestamp,
          HeadTimestamp: action.data.HeadTimestamp,
          Name: action.data.Name,
          Songs: action.data.Songs
        }
      }
    }
    case Actions.Types.APPEND_CURRENT_CHAT_MESSAGE: {
      return {
        ...state,
        CurrentChat: {
          ...state.CurrentChat,
          Messages: [...(state?.CurrentChat?.Messages ? state?.CurrentChat?.Messages : []), ...action.data]
        }
      }
    }
    case 'EXIT_CHAT': {
      if (state.CurrentChat?.ScheduledTaskId != null) {
        clearInterval(state.CurrentChat?.ScheduledTaskId);
      }
      MusicKit.getInstance().player.pause();
      MusicKit.getInstance().setQueue({});
      MusicKit.configure({
        developerToken: 'eyJhbGciOiJFUzI1NiIsInR5cCI6IkpXVCIsImtpZCI6IjNBTDk4M1lBS1gifQ.eyJpYXQiOjE2NjcxNDgwMzYsImV4cCI6MTY4MjcwMDAzNiwiaXNzIjoiTk43MkZINllSMyJ9.UfXiBOYyVOGOSs98-Mc07KxdA8SWp_7g-ZyYjHPkmV8Atf-WeV_LZtI8vS81-xvvB9yTGx_Wgt9fLnt_tmDNDw',
        app: {
          name: 'DogeDock',
          build: '1978.4.1'
        }
      });
      return {
        ...state,
        CurrentChat: null
      }
    }
    case 'HIDE_CHAT_MESSAGE_WINDOW': {
      return {
        ...state,
        CurrentChat: {
          ...state.CurrentChat,
          MessageWindowShow: false
        }
      }
    }
    case 'SHOW_CHAT_MESSAGE_WINDOW': {
      f7.views.current.router.navigate('/chat/');
      return {
        ...state,
        CurrentChat: {
          ...state.CurrentChat,
          MessageWindowShow: true
        }
      }
    }
    case Actions.Types.UPDATE_CHAT_LIST: {
      return {
        ...state,
        ChatList: action.data
      }
    }
    case Actions.Types.UPDATE_LIBRARY_PLAYLISTS: {
      return {
        ...state,
        My: {
          ...state.My,
          LibraryPlaylists: action.data,
        }
      }
    }
    case Actions.Types.UPDATE_MUSIC_PLAYER_DETAIL: {
      return {
        ...state,
        MusicPlayer: action.data
      }
    }
    case Actions.Types.UPDATE_USER_ID: {
      return {
        ...state,
        User: {
          ...state.User,
          Id: action.data
        }
      }
    }
  }
}


const AppComponent = () => {

  const [scheduledTaskId, setScheduledTaskId] = useState(undefined);

  const [state, dispatch] = useReducer(ActionReducer, {
    ChatList: [],
    My: {
      LibraryPlaylists: []
    }
  });
  const stateRef = useRef(state);
  stateRef.current = state;

  if (scheduledTaskId != undefined) {
    clearInterval(scheduledTaskId);
  }

  const [activeTab, setActiveTab] = useState('');
  const previousTab = useRef(null);
  useEffect(() => {
    setInterval(() => {
      Actions.UpdateMusicPlayerDetail(MusicKit.getInstance().player.currentPlaybackTime / MusicKit.getInstance().player.currentPlaybackDuration * 100,
        MusicKit.getInstance().player.nowPlayingItem?.attributes?.name,
        MusicKit.getInstance().player.nowPlayingItem?.attributes?.artwork?.url?.replace('{w}x{h}', '96x96'),
        dispatch)
    }, 1000)
  }, []);
  useEffect(() => {
    previousTab.current = activeTab;
  }, [activeTab]);

  useEffect(() => {
    // Fix viewport scale on mobiles
    if ((f7.device.ios || f7.device.android) && f7.device.standalone) {
      const viewPortContent = document.querySelector('meta[name="viewport"]').getAttribute('content');
      document.querySelector('meta[name="viewport"]').setAttribute('content', `${viewPortContent}, maximum-scale=1, user-scalable=no`);
    }
  }, []);

  // Framework7 Parameters
  const f7params = {
    name: 'App Store',
    theme: 'ios',
    routes,
    autoDarkTheme: true,
    navbar: {
      // TODO
      scrollTopOnTitleClick: false
    },
  };
  if (process.env.NODE_ENV === 'production') {
    // Register service worker in production mode only
    PWA.init();
  }

  function onTabLinkClick(tab) {
    if (previousTab.current !== activeTab) return;
    if (activeTab === tab) {
      $(`#view-${tab}`)[0].f7View.router.back();
    }
  }

  useEffect(async () => {

    async function getMessageScheduled() {

      try {

        let userId = localStorage.getItem("UserId");

        if (state.User?.Id == null && userId != null) {
          dispatch({ type: Actions.Types.UPDATE_USER_ID, data: localStorage.getItem("UserId") })
        }

        if (stateRef.current?.CurrentChat?.Id != null) {
          if (stateRef.current.CurrentChat.Messages == null || stateRef.current.CurrentChat.Messages.length == 0) {
            Actions.FetchMessageAsync(stateRef.current.CurrentChat.Id, -2, dispatch);
          } else {
            Actions.FetchMessageAsync(stateRef.current.CurrentChat.Id, stateRef.current.CurrentChat.Messages.at(-1).ID, dispatch);
          }
        }
      } finally {
        setTimeout(getMessageScheduled, 1000);
      }
    }
    getMessageScheduled();

    function getIndexOfSongId(sid) {
      for (let i = 0; i < MusicKit.getInstance().player.queue.items.length; i++) {
        if (MusicKit.getInstance().player.queue.items[i].id == sid) {
          return i;
        }
      }
      return -1;
    }
    document.addEventListener("click", () => syncMusicPlayer(false));
    MusicKit.getInstance().player.__next = MusicKit.getInstance().player._next;
    MusicKit.getInstance().player._next = function () {
      syncMusicPlayer(false);
    }
    async function syncMusicPlayer(infinity = false) {
      if (infinity) {
        setTimeout(() => syncMusicPlayer(true), 5000)
      }
      try {
        let headTimestamp = stateRef.current.CurrentChat?.HeadTimestamp;
        let Songs = stateRef.current.CurrentChat?.Songs;
        for (let idx = 0; Songs?.length > 0; idx++) {
          let i = idx % Songs?.length;
          if (headTimestamp + Songs[i].Duration > Date.now() / 1000) {
            if(!MusicKit.getInstance().player._currentPlayer.audio.paused && !MusicKit.getInstance().player._currentPlayer.isPlaying){
              console.log("alert!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!")
              MusicKit.configure({
                developerToken: 'eyJhbGciOiJFUzI1NiIsInR5cCI6IkpXVCIsImtpZCI6IjNBTDk4M1lBS1gifQ.eyJpYXQiOjE2NjcxNDgwMzYsImV4cCI6MTY4MjcwMDAzNiwiaXNzIjoiTk43MkZINllSMyJ9.UfXiBOYyVOGOSs98-Mc07KxdA8SWp_7g-ZyYjHPkmV8Atf-WeV_LZtI8vS81-xvvB9yTGx_Wgt9fLnt_tmDNDw',
                app: {
                  name: 'DogeDock',
                  build: '1978.4.1'
                }
              });
            }
            console.log(Songs[i]);
            // free account can't get all songs
            if (MusicKit.getInstance().player.queue.items.length == 0 && Songs.length != 0) {
              await MusicKit.getInstance().setQueue({ songs: Songs.map(s => s.SID) });
            }

            if (MusicKit.getInstance().player.nowPlayingItem?.attributes?.playParams?.id != Songs[i].SID) {
              console.log("change", Songs[i]);
              await MusicKit.getInstance().setQueue({song:Songs[i].SID});
              await MusicKit.getInstance().player.changeToMediaAtIndex(0);
            }
            // TODO free account 30 s
            // 
            // ??????
            if (MusicKit.getInstance().player.currentBufferedProgress * MusicKit.getInstance().player.currentPlaybackDuration / 100 > Date.now() / 1000 - headTimestamp) {
              if (Math.abs(MusicKit.getInstance().player.currentPlaybackTime - (Date.now() / 1000 - headTimestamp)) > 1.5) {
                console.log("seek", Date.now() / 1000 - headTimestamp);
                await MusicKit.getInstance().player.seekToTime(Date.now() / 1000 - headTimestamp);
                console.log("seek end")
              }
            }

            if (!MusicKit.getInstance().player.isPlaying) {
              console.log("play !!!!!!!!!!!!!!", MusicKit.getInstance().player.isPlaying, MusicKit.getInstance().player.isReady);
              await MusicKit.getInstance().player.play();
            }

            //
            break;
          } else {
            headTimestamp += stateRef.current.CurrentChat?.Songs[i].Duration;
          }
        }
      } catch (e) {
        console.error(e);
      }
    }
    syncMusicPlayer(true);
  }, [])

  return (

    <App {...f7params}>
      <StateContext.Provider value={{ state: state, dispatch: dispatch }}>

        <View url='/'>
          {state.CurrentChat ? <Navbar scrollTopOnTitleClick={false} className='chat-music-navbar'>
            <div className='title' style={{ width: "100%" }}>
              <Row noGap className='chat-music-player' style={{ display: "flex" }}>
                <img
                  slot="media"
                  src={state.MusicPlayer?.Image}
                  width="96px"
                  height='96px'
                />
                <Row noGap style={{ width: "0px" }}>
                  <Col width='100' style={{ height: '96px' }}>
                    <Row noGap style={{ height: '92px' }}>
                      <Col width='50' style={{ overflow: 'hidden' }}>
                        <p> {state.CurrentChat?.Name} </p>

                        <p>{state.MusicPlayer?.Name}</p>
                      </Col>
                      <Col width='50' style={{ height: '92px' }}>
                        <Row noGap style={{ height: '56px' }} >
                        </Row>
                        <Row noGap style={{ height: '36px' }} >

                          <Col width='25'>
                            <Button color='red' iconF7='xmark' onClick={() => { console.log(f7.views.current.router.back()); dispatch({ type: "EXIT_CHAT" }) }}></Button>
                          </Col>

                          <Col width='25'>
                            <Button iconF7="person_2_fill" popupOpen=".chat-member-list" >
                            </Button>
                          </Col>
                          <Col width='25'>
                            <Button iconF7="list_number" popupOpen=".chat-music-list" >
                            </Button>
                          </Col>

                          <Col width='25'>
                            <Button onClick={() => {
                              if (state.CurrentChat?.MessageWindowShow) {
                                f7.views.current.router.back();
                                dispatch({ type: "HIDE_CHAT_MESSAGE_WINDOW" });
                              } else {
                                f7.views.current.router.navigate('/chat/123');
                                dispatch({ type: "SHOW_CHAT_MESSAGE_WINDOW" });
                              }
                            }} iconF7="chat_bubble_fill" fill={state.CurrentChat?.MessageWindowShow} />

                          </Col>

                        </Row>
                      </Col>

                    </Row>
                    <Row style={{ height: '4px' }}>
                      <Progressbar style={{ height: '4px' }} progress={state.MusicPlayer?.Progress} id="chat-music-player-progressbar" />
                    </Row>
                  </Col>
                </Row>
              </Row>
            </div>
          </Navbar> : null}

          <Popup className="chat-music-list" swipeToClose>
            <Page>
              <Navbar title="">
                <NavRight>
                  <Link popupClose>Close</Link>
                </NavRight>
              </Navbar>

              <List mediaList>
                {state.CurrentChat?.Songs?.map(song => (
                  <ListItem title={song.Name}>

                  </ListItem>
                ))}
              </List>
            </Page>
          </Popup>
          <Popup className="chat-member-list" swipeToClose>
            <Page>
              <Navbar title="chat-member-list">
                <NavRight>
                  <Link popupClose>Close</Link>
                </NavRight>
              </Navbar>
              <Block>Coming soon</Block>
            </Page>
          </Popup>
        </View>
      </StateContext.Provider>

    </App>
  );
};

export default AppComponent;
