import React, { useContext, useEffect, useRef, useState } from 'react';

import {
  Block,
  Button,
  Input,
  Navbar,
  Page,
  Messages,
  MessagesTitle,
  Message,
  Messagebar,
  Link,
  View,
  MessagebarAttachments,
  MessagebarAttachment,
  MessagebarSheet,
  MessagebarSheetImage,
  f7ready,
  f7,
  List,
  ListItem,
  Row,
  Col,
} from 'framework7-react';

import AppstorePage from '../components/AppstorePage';
import { Actions } from '../state/Actions';
import { StateContext } from '../state/StateContext';
import LibraryPlaylist from './LibraryPlaylist';

const Arcade = () => {
  const { state, dispatch } = useContext(StateContext);
  useEffect(() => {
    Actions.FetchLibraryPlayListsAsync(state, dispatch);
  }, [])
  return (
    <AppstorePage title="">
      <Button onClick={async () => {
        await Actions.FetchLibraryPlayListsAsync(state, dispatch);
      }}></Button>
      <List mediaList>
        {
          state.My.LibraryPlaylists.map(palylist => (
            <ListItem title={palylist.attributes.name} key={palylist.id} popupOpen='.popup-library-playlist' onClick={async () => {
              await Actions.OpenPlaylistAsync(palylist.id, dispatch);
            }} link="#" >
              <img style={{ width: "80px", height: "80px" }} slot="media" src={palylist.attributes?.artwork?.url} alt="" />
            </ListItem>
          ))
        }
      </List>

      <LibraryPlaylist>

      </LibraryPlaylist>
    </AppstorePage>
  );
};

export default Arcade;
